    <div class="row withdrawing-section">
      <div class="col-lg-6 mt-2 d-flex">
        <img src="../../assets/img/part-withdraw.png" />
      </div>
      <div class="col-lg-6 mt-5 mb-5">
        <div
          class="withdrawing-content-1"
        >In order to withdraw tokens from the Cryptoraves platform, users must complete several steps.</div>
        <div class="space-50"></div>
        <div
          class="withdrawing-content-1"
        >This feature is currenly being built, & instructions will be rolled out in Q1 2020. Check back soon!</div>
      </div>
    </div>
  </div>
</template>

<script>
import SectionHeader from "../../components/ui/SectionHeader";
export default {
  name: "WithdrawingToken",
  components: {
    SectionHeader
  }
};
</script>

<style scoped>
.withdrawing-section {
  padding-top: 40px;
  padding-bottom: 100px;
  font-family: Montserrat;
  color: #002665;
}
img {
  margin: auto;
  width: 450px;
}
.withdrawing-content-1 {
  font-style: italic;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.333;
  text-align: left;
}
</style>



